<template>
  <Card class="card">
     <Breadcrumb>
        <BreadcrumbItem>货主中心</BreadcrumbItem>
        <BreadcrumbItem>货主列表</BreadcrumbItem>
    </Breadcrumb>
      <div class='box-container'>
        <div style="margin:20px 0 40px 0">
             <Input placeholder="请输入货主公司" style='width:220px' v-model="name" @keyup.enter.native="search()" /> <Button type="primary" @click="search()">搜索</Button>
        </div>
        <Table border :columns="columns1" :data="data1"></Table>
        <Page :total="total"  @on-page-size-change='sizeChange'
         class="pages" @on-change="changePage" :current="currentPage" :page-size="size"  show-elevator show-sizer  />
      </div>
  </Card>
</template>
<script>
    export default {
        data () {
            return {
                name:"",
                total:20,
                columns1: [
                    {
                        title: '#',
                        key: 'Index',
                        width: 70,
                        align:'center',
                    },
                    {
                        title: '货主公司',
                        key: 'company',
                        align:'center',
                    },
                    {
                        title: '联系人',
                        key: 'concact',
                         align:'center',
                    },
                    {
                        title: '联系电话',
                        key: 'phone',
                         align:'center',
                    },
                    {
                        title: '偏好航线',
                        key: 'preLine',
                         align:'center',
                    },
                    {
                        title: '偏好船型',
                        key: 'preShip',
                         align:'center',
                    },
                    {
                        title: '偏好货种',
                        key: 'preGoods',
                         align:'center',
                    },
                    {
                        title: '详情',
                        key: 'detail',
                         align:'center',
                         render: (h, params) => {
                           return h('Button', {
                             props:{
                               type:"primary"
                             },
                              style:{
                                display:'inline-block',
                                width:100,
                                background:'red;'
                              },
                              on:{
                                click:()=>{
                                    // console.log(123,params)
                                    let routeUrl = this.$router.resolve({
                                      path: "/hzCenter/hzdetail",
                                      query: {code:params.row.code}
                                });
                                window.open(routeUrl .href, '_blank');

                                }
                              }
                           },'查看')
                       }
                    },
                ],
                data1: [],
                currentPage:1,
                size:10,
            }

        },
        watch:{
            name(){
              if(!this.name){
                 this.size = 10
                 this.huozhuList()
                 localStorage.removeItem('searchInfo')
              }
            }
        },
        methods:{
        async  huozhuList(){
            let data = {
              currentPage:this.currentPage,
              size:this.size,
            }
           try{
                  let res = await this.$http.huozhuList(data)
                  if(0==res.data.status &&  res.data.result instanceof Array){
                      this.total = res.data.total
                      let result = res.data.result
                      this.data1 = []
                      this.commonFn(result)
                  }
                  else{
                    this.$Notice.error({
                      desc:'暂无数据'
                    })
                    this.data1 = []
                    this.total=0
                  }

              }
              catch(err){
                this.$Notice.error({
                  desc:'服务器错误,请稍后重试.'
                })
                throw Error(err)

              }
          },
        changePage(currentPage){
            this.currentPage = currentPage;
            if(localStorage.searchInfo){
                this.search(currentPage)
            }
            else{
              this.huozhuList()
            }
          },
      sizeChange(size){
              this.size = size
              if(!this.name){
                this.huozhuList()
              }
              else{
                this.search()
              }
          },
          // 公共函数
       commonFn(result){
                 result.forEach((item,index) => {
                          this.data1.push({
                              Index:Number((this.currentPage-1)*this.size)+Number(index)+1,
                              company: item.companyName,
                              concact: item.clientName,
                              phone: item.phone,
                              preLine: item.preferVoyage,
                              preShip: item.preferShipType,
                              preGoods: item.preferCargo,
                              companyId:item.companyId,
                              code:item.code,
                              detail:'查看'
                          })
                      });
          },

          // 查询
        async  search(currentPage){
          if(!this.name){
            this.$Notice.error({
              desc:"请输入货主公司"
            })
            return false
          }
               currentPage ? this.currentPage = currentPage : this.currentPage =1
             let data = {
                  currentPage:this.currentPage,
                  size:this.size,
                  trem:this.name,
             }
             try{
                  let res = await this.$http.selectCargoOwnerInfo(data)
                   this.total = res.data.total
                   this.data1 = []
                   if(!res.data.status &&  res.data.result instanceof Array){
                      localStorage.setItem('searchInfo',this.name)
                       this.$Notice.success({
                          desc:'查询成功'
                        })
                      let result = res.data.result
                      this.commonFn(result)
                   }
                   else{
                     this.$Notice.error({
                        desc:'暂无数据'
                      })
                      return false
                   }
             }
             catch(err){
               localStorage.removeItem('searchInfo')
               this.$Notice.error({
                  desc:'服务器错误,请稍后重试.'
                })
                throw Error(err)
             }


          },
        },
        created(){
          this.huozhuList()
        },

        destroyed(){
           localStorage.removeItem('searchInfo')
        }
    }
</script>
<style lang="scss" scoped>
.box{
  margin-top: 70px;
}
 .ivu-table td, .ivu-table th {
    height: 18px;
}
/deep/  .ivu-page{
  position: relative;
}
</style>



